@import url(https://fonts.googleapis.com/css2?family=Source+Code+Pro:ital,wght@0,900;1,400&display=swap);
body {
  --main-text-color: #EEEEEE;
  --main-bg-color: hsla(212, 29%, 15%, 1);
  --secondary-bg-color: hsla(290, 50%, 35%, 0.75);
  margin: 0;
  font-family: 'Source Code Pro', monospace;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}
.App {
  text-align: center;
  background-color: var(--main-bg-color);
  /* background-image: url('img-noise-500x500.png'); */
  color: var(--main-text-color);
  margin: 0;
  padding: 0;
  height: 100%;
  min-height: 100vh;
}

.App-logo {
  height: 40vmin;
  pointer-events: none;
}

.App-header {
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
}

.App-header h1, p {
  margin: 0;
}

header.App-header  {
  padding: 0.5rem;
}

p {
  margin: 0 1rem;
}

.App-link {
  color: #61dafb;
}

.drum-pad {
  border-radius: 0.5rem;
  box-sizing: border-box;
  padding: 10px;
  min-height: 20px;
  background-color: var(--secondary-bg-color);
  display: flex;
  flex-flow: column;
  /* justify-content: center; */
  align-items: center;
}

.drum-pad:focus {
  outline: 0;
}

.drum-pad header {
  font-size: 3rem;
}

.playing {
 animation: 4s linear filter-animation infinite;
}

@keyframes filter-animation {
  0% {
    -webkit-filter: hue-rotate(0deg)
            blur(0px)
            brightness(1.1)
            drop-shadow(0.1rem 0.1rem 0px var(--main-text-color));
            filter: hue-rotate(0deg)
            blur(0px)
            brightness(1.1)
            drop-shadow(0.1rem 0.1rem 0px var(--main-text-color));
  }
  50% {
    -webkit-filter: hue-rotate(5deg)
            blur(1px)
            brightness(1.8)
            drop-shadow(0.5rem 0.5rem 0.5rem var(--main-text-color));
            filter: hue-rotate(5deg)
            blur(1px)
            brightness(1.8)
            drop-shadow(0.5rem 0.5rem 0.5rem var(--main-text-color));
  }
  100% {
    -webkit-filter: hue-rotate(0deg)
            blur(0px)
            brightness(1.1)
            drop-shadow(0.1rem 0.1rem 0px var(--main-text-color));
            filter: hue-rotate(0deg)
            blur(0px)
            brightness(1.1)
            drop-shadow(0.1rem 0.1rem 0px var(--main-text-color));
  }

}

@media (min-width: 360px) {
  .drum-pad {
    padding: 30px;
    min-height: 100px;
  }

  .drum-pad header {
    font-size: 4rem;
  }

}
#display {
  background-color: hsla(0, 45%, 35%, 1);
  grid-column: 1 / -1;
  min-height: 10vh; 
  display: flex;
  flex-flow: column;
  justify-content: center;
  padding: 0 1rem;
  font-family: 'Source Code Pro', monospace;
  font-weight: 400;
  font-style: italic;
}


#drum-machine-container {
  box-sizing: border-box;
  display: flex;
  flex-flow: column;
}

#drum-machine {
  display: grid;
  grid-template-columns: repeat(auto-fit, minmax(100px, 1fr));
  grid-gap: 0.5rem;
  gap: 0.5rem;
  margin: 0 1rem;
}

/* Sneaky force square ratios hack
#drum-machine > *::before {
  --aspect-ratio: 1;
  content: "";
  display: inline-block;
  width: 1px;
  height: 0;
  padding-bottom: calc(100% / (var(--aspect-ratio)));
} */



@media (min-width: 360px) {
  #display {
    min-height: 10vh; 
    padding: 0 1rem;
    margin: 0.5rem 4rem;
    padding: 1rem;
    font-size: 1.2rem;
  }

  #drum-machine {
    grid-template-columns: repeat(auto-fit, minmax(150px, 1fr));
    /* gap: 0.5rem;
    margin: 0 1rem; */
  }

}

